<template>
  <div id="elfsight-widget-container">
    <!-- Spinner Loader -->
    <div v-if="loading" class="loader"></div>

    <!-- Elfsight Widget Placeholder -->
    <div v-else class="elfsight-app-2c6be146-097a-4d89-9f53-d99ed86979eb" data-elfsight-app-lazy></div>
  </div>
</template>

<script>
export default {
  name: 'ElfsightWidget',
  data() {
    return {
      loading: true, // Initial state as loading
    };
  },
  mounted() {
    this.ensureElfsightScript();
  },
  methods: {
    ensureElfsightScript() {
      const scriptId = 'elfsight-platform-script';
      if (document.getElementById(scriptId)) {
        // Elfsight script already loaded, so we remove the loader
        this.loading = false;
        return;
      }
      const script = document.createElement('script');
      script.id = scriptId;
      script.src = 'https://static.elfsight.com/platform/platform.js';
      script.defer = true;
      document.head.appendChild(script);

      script.onload = () => {
        console.log('Elfsight platform script loaded successfully');
        this.loading = false; // Remove loader after script is loaded
      };

      script.onerror = (error) => {
        console.error('Error loading Elfsight platform script:', error);
        this.loading = false; // Remove loader even if there is an error
      };
    },
  },
};
</script>

<style scoped>
.loader {
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 2s linear infinite;
  margin: 20px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Style for your widget container */
#elfsight-widget-container {
  min-height: 50px; /* Ensure container has space for the loader */
}
</style>
